<template> 
  <div class="app-container">
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float:right"
          type="primary"
          @click="handleSearchList()"
          size="small">
          查询搜索
        </el-button>
        <el-button
          style="float:right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
            <el-form-item label="开始时间：">
              <el-date-picker
                class="input-width"
                v-model="listQuery.startTimeStr"
                value-format="yyyy-MM-dd HH:mm:ss"
                type="datetime"
                placeholder="请选择时间">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="结束时间：">
              <el-date-picker
                class="input-width"
                v-model="listQuery.endTimeStr"
                value-format="yyyy-MM-dd HH:mm:ss"
                type="datetime"
                placeholder="请选择时间">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="域名：">
              <el-input v-model="listQuery.domainName" class="input-width" placeholder="请求的域名"></el-input>
            </el-form-item>
            <el-form-item label="是否审核中：">
              <el-select v-model="listQuery.isCheck" class="input-width" placeholder="请选择">
                <el-option label="是" value="true"></el-option>
                <el-option label="否" value="false"></el-option>
              </el-select>
            </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
    </el-card>
    <div class="table-container">
      <el-table ref="couponTable"
                :data="list"
                style="width: 100%;"
                @selection-change="handleSelectionChange"
                v-loading="listLoading" border>
        <el-table-column type="selection" width="60" align="center"></el-table-column>
        <el-table-column label="请求的域名" width="100" align="center">
          <template slot-scope="scope">{{scope.row.domainName}}</template>
        </el-table-column>
        <el-table-column label="请求IP" align="center"  width="100">
          <template slot-scope="scope">{{scope.row.requestIp}}</template>
        </el-table-column>
        <el-table-column label="IP所属地区" width="100" align="center">
          <template slot-scope="scope">{{scope.row.ipRegion}}</template>
        </el-table-column>
        <el-table-column label="UA"  align="center">
          <template slot-scope="scope">{{scope.row.userAgent}}</template>
        </el-table-column>

        <el-table-column label="是否审核中" width="100" align="center">
          <template slot-scope="scope">{{scope.row.checkStatus | formatBoolean}}</template>
        </el-table-column>

        <el-table-column label="请求时间" width="180" align="center">
          <template slot-scope="scope">{{scope.row.createTime|formatDate}}</template>
        </el-table-column>
        <el-table-column label="请求头信息" width="100" align="center">
          <template slot-scope="scope">
            <el-button type="primary" @click="showHeaderInfo(scope.row.headerInfo)">查看</el-button>
          </template>
        </el-table-column>

      </el-table>
      <el-dialog :visible.sync="dialogVisible" title="请求头信息">
        <pre class="formatted-json">{{ formattedHeaderInfo }}</pre>
        <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">关闭</el-button>
      </span>
      </el-dialog>
    </div>
    <div class="pagination-container">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :current-page.sync="listQuery.pageNum"
        :page-size="listQuery.pageSize"
        :page-sizes="[5,10,15]"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
  import {fetchList} from '@/api/cloakLog';
  import {formatDate} from '@/utils/date';
  const defaultListQuery = {
    pageNum: 1,
    pageSize: 10,
    startTimeStr: null,
    endTimeStr: null,
    startTime: null,
    endTimeTime: null,
    isCheck: null // 初始值可以是 null, true 或 false
  };

  const defaultTypeOptions=[
    {
      label: '全场赠券',
      value: 0
    },
    {
      label: '会员赠券',
      value: 1
    },
    {
      label: '购物赠券',
      value: 2
    },
    {
      label: '注册赠券',
      value: 3
    }
  ];
  export default {
    name:'cloakRequestLogList',


    data() {
      return {
        listQuery:Object.assign({},defaultListQuery),
        typeOptions:Object.assign({},defaultTypeOptions),
        list:null,
        total:null,
        listLoading:false,
        multipleSelection:[],
        dialogVisible: false, // 对话框显示状态
        formattedHeaderInfo: '' // 格式化后的请求头信息
      }
    },
    created(){
      this.getList();
    },
    filters:{
      formatType(type){
        for(let i=0;i<defaultTypeOptions.length;i++){
          if(type===defaultTypeOptions[i].value){
            return defaultTypeOptions[i].label;
          }
        }
        return '';
      },
      formatUseType(useType){
        if(useType===0){
          return '全场通用';
        }else if(useType===1){
          return '指定分类';
        }else{
          return '指定商品';
        }
      },
      formatPlatform(platform){
        if(platform===1){
          return '移动平台';
        }else if(platform===2){
          return 'PC平台';
        }else{
          return '全平台';
        }
      },
      formatBoolean(boolean){
        if(boolean){
          return '是';
        }else{
          return '否';
        }
      },
      formatDate(time){
        if(time==null||time===''){
          return 'N/A';
        }
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm:ss')
      },
      formatStatus(endTime){
        let now = new Date().getTime();
        let endDate = new Date(endTime);
        if(endDate>now){
          return '未过期'
        }else{
          return '已过期';
        }
      }
    },
    methods:{
      handleResetSearch() {
        this.listQuery = Object.assign({}, defaultListQuery);
      },
      handleSearchList() {
        this.listQuery.pageNum = 1;
        this.getList();
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handleSizeChange(val) {
        this.listQuery.pageNum = 1;
        this.listQuery.pageSize = val;
        this.getList();
      },
      handleCurrentChange(val) {
        this.listQuery.pageNum = val;
        this.getList();
      },
      showHeaderInfo(headerInfo) {
        // 多次反序列化，直到得到正确的JSON对象
        let parsedHeaderInfo = headerInfo;
        try {
          while (typeof parsedHeaderInfo === 'string') {
            parsedHeaderInfo = JSON.parse(parsedHeaderInfo);
          }
        } catch (error) {
          console.error('Failed to parse header info:', error);
          this.formattedHeaderInfo = 'Invalid JSON format';
          this.dialogVisible = true;
          return;
        }
        // 格式化JSON数据并显示在对话框中
        this.formattedHeaderInfo = JSON.stringify(parsedHeaderInfo, null, 2);
        this.dialogVisible = true;
      },
      getList(){
        this.listLoading=true;
        this.listQuery.startTime = this.listQuery.startTimeStr ? new Date(this.listQuery.startTimeStr).getTime() : null;
        this.listQuery.endTime =  this.listQuery.endTimeStr ? new Date( this.listQuery.endTimeStr).getTime() : null;
        fetchList(this.listQuery).then(response=>{
          this.listLoading = false;
          this.list = response.data.records;
          this.total = response.data.total;
        });
      }
    }
  }
</script>
<style scoped>
  .input-width {
    width: 203px;
  }
</style>





